
class WifetUtil {
    static getRandomInt(min, max) {
        min = Math.ceil(min);
        max = Math.floor(max);
        return Math.floor(Math.random() * (max - min + 1)) + min;
    }

    static makeRandomData(cnt = 200, min, max, onlyUp = false, inputData = [], diff = 5) {
        const data = [];
        let current = 0;
        let skew = diff;
        for (let i = 0, l = cnt; i < l; i += 1) {
            if (inputData[i] && inputData[i].y) {
                skew = WifetUtil.getRandomInt(skew - diff, skew + diff);
                current = inputData[i].y + skew;
                if (skew > diff) { skew = 0; }
            } else if (!current) {
                current = WifetUtil.getRandomInt(min, max);
            } else if (onlyUp) {
                current = WifetUtil.getRandomInt(current, current + diff);
            } else {
                current = WifetUtil.getRandomInt(current - diff, current + diff);
            }
            data.push({ x: i, y: current })
        }
        return data;
    }

    static addPercent(inputData = [], percent = 10, $max = 0) {
        let max = $max;
        for (let i = 0, l = inputData.length; i < l; i += 1) {
            if (inputData[i] && inputData[i].y && inputData[i].y > max) {
                max = inputData[i].y;
            }
        }
        const data = [];
        for (let i = 0, l = inputData.length; i < l; i += 1) {
            if (inputData[i] && inputData[i].y) {
                const val = inputData[i].y;
                const newVal = val + (percent * max / 100);
                data.push({
                    x: i,
                    y: newVal,
                });
            }
        }
        return data;
    }
}

export default WifetUtil;
