import { h, Component } from 'preact';
import { observer } from 'mobx-preact';

import LineGraph from '../../components/lineGraph';
import wutil from '../../lib/wifet-util';

const MIN = 20;
const MAX = 60;

const initialState = {
	aiData: wutil.makeRandomData(500, MIN, MAX),
	windData: wutil.makeRandomData(500, MIN, MAX),
};

@observer
class Demand extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	render() {
		const { windData, aiData } = this.state;
		return (
			<div class='container-fluid mt-5 pt-2'>
				<div class='row'>
					<div class='col-12'>
						<h1>Demand</h1>
						<LineGraph 
							data={windData}
							dataB={aiData}
							legend='Demand'
							legendB='Demand AI prediction'
							width={1200}
							height={500}
							yMax={100}
							y2Max={100}
							showYTicks={1}
							yTicks='["100 MWh", "75 MWh", "50 MWh", "25 MWh", "0 MWh"]'
							showXTicks={1}
							marker={400}
						/>
					</div>
				</div>
			</div>
		);
	}
}

export default Demand;
